import React from 'react'
import PropTypes from 'prop-types'
import {PageHeader, Skeleton, Result, Button} from 'antd'
import './page2.less'

class Page2 extends React.Component {
    constructor (props) {
        super(props)
        this.state = {
            loadingStatus: false
        }
    }
    componentDidMount () {
        this.setState({
            loadingStatus: true
        })
        setTimeout(() => {
            this.setState({
                loadingStatus: false
            })
        }, 2000)
    }
    openWindow = (url) => {
        window.open(url)
    }
    render () {
        return (
            <div className="moduleC-page2">
                <PageHeader
                    className="site-page-header"
                    title="moduleC-page2"
                    subTitle="moduleCpage2"
                />
                <Skeleton active loading={this.state.loadingStatus}/>
                <Skeleton active loading={this.state.loadingStatus}/>
                <Skeleton active loading={this.state.loadingStatus}/>
                <div style={{display: this.state.loadingStatus ? 'none' : 'block'}}>
                    <Result
                        status="success"
                        title="!"
                        subTitle="DEMOsingle-spamess-clisingle-spa."
                        extra={[
                        <Button type="primary" key="1" onClick={() => {this.openWindow('https://single-spa.js.org')}}>
                            single-spa
                        </Button>,
                        <Button type="primary" key="2" onClick={() => {this.openWindow('https://www.messcli.cn/#/')}}>
                            mess-cli
                        </Button>,
                        <Button key="3"></Button>,
                        ]}
                    />
                </div>
            </div>
        )
    }
}

export default Page2