import React from 'react'
import { Alert, Tag, Card, Button } from 'antd'
import './homePage.less'
import {singleSpa} from '../../utils/singleSpa'

class HomePage extends React.Component {
    openWindow = (url) => {
        window.open(url)
    }
    goPage = (url) => {
        singleSpa.router.push(url)
    }
    render () {
        let tagClassName = this.props.auth !== 'admin'  ? 'moduleC-home-primary' : 'moduleC-home-warning'
        return (
            <div className="moduleC-home">
                <Alert message="single-spamess-cli" type="success" showIcon />
                <p style={{marginTop: '20px'}}>
                    <Tag className={tagClassName}>{this.props.auth}</Tag >
                </p>
                <Card
                className="moduleC-home-card"
                title={<div><Tag>single-spa-react</Tag><Tag>react</Tag><Tag>moduleC</Tag></div>}>
                    <p>1. moduleC<Tag>antd-design</Tag><Tag>moduleCantd</Tag></p>
                    <p>2. moduleC<Tag>redux</Tag><Tag></Tag><Tag></Tag></p>
                    <p>3. 
                        single-spa
                        <Tag>single-spa
                            <Button type="link" onClick={() => {this.openWindow('https://single-spa.js.org')}}>https://single-spa.js.org</Button>
                        </Tag>
                        mess-cli
                        <Tag>mess-cli
                            <Button type="link" onClick={() => {this.openWindow('https://www.messcli.cn/#/')}}>https://www.messcli.cn/#/</Button>
                        </Tag>
                    </p>
                </Card>
                <div className='moduleC-home-but'>
                    <Button type='primary' onClick={() => {this.goPage('/moduleC/page1')}}>moduleC-page1</Button>
                    <Button type='primary' onClick={() => {this.goPage('/moduleC/page2')}}>moduleC-page2</Button>
                </div>
            </div>
        )
    }
}

export default HomePage